html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--main-font);
  color: var(--light-text-grey);
}

:root {
  --dark-grey: hsl(0, 0%, 7%);
  --light-grey: hsl(0, 0%, 12%);
  --dark-text-grey: hsl(231, 6%, 43%);
  --light-text-grey: hsl(240, 3%, 92%);
  --accent-pink: hsl(340, 62%, 53%);
  --darker-accent-pink: hsl(340, 62%, 23%);
}

:root {
  --main-font: "Poppins", sans-serif;
  --accent-font: "Cutive Mono", monospace;
}

.accent {
  color: var(--accent-pink);
}

.special-txt {
  font-family: var(--accent-font);
  font-size: rem(16);
  padding-bottom: 20px;
  letter-spacing: 2px;
}

.p-r-20 {
  padding-right: rem(20);
  display: none;
}

.p-r-50 {
  padding-right: rem(20);
}

.container {
  min-height: 100vh;
}
.container__maxwidth {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 1.5625rem;
}

.container.header {
  background-color: var(--dark-grey);
  padding: 4.6875rem;
}
@media (max-width: 39.9375em) {
  .container.header {
    padding: 1.5625rem;
  }
}

.container.about {
  background-color: var(--light-grey);
  padding: 4.6875rem;
}
@media (max-width: 39.9375em) {
  .container.about {
    padding: 1.5625rem;
  }
}

.container.projects {
  background-color: var(--dark-grey);
  padding: 4.6875rem;
}
@media (max-width: 39.9375em) {
  .container.projects {
    padding: 1.5625rem;
  }
}

.container.contact {
  background-color: var(--light-grey);
  padding: 4.6875rem;
}
@media (max-width: 39.9375em) {
  .container.contact {
    padding: 1.5625rem;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
}

h1 {
  font-family: var(--main-font);
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 59.375em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.375rem;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: var(--light-text-grey);
}

a:visited {
  color: var(--light-text-grey);
}

.nav__list {
  display: flex;
  justify-content: flex-end;
  padding: 3.125rem 0;
}
@media (max-width: 71.8125em) {
  .nav__list {
    justify-content: center;
    align-items: center;
  }
}
@media (max-width: 39.9375em) {
  .nav__list {
    padding: 1.5625rem, 0;
  }
}
.nav__list--item {
  padding-left: 1.25rem;
}
.nav__list--item a {
  font-size: 1.5625rem;
  font-weight: 400;
  letter-spacing: 1.2px;
  text-transform: capitalize;
}
@media (max-width: 71.8125em) {
  .nav__list--item a {
    font-size: 1.25rem;
    font-weight: 300;
  }
}
.nav__list--item a:hover,
.nav__list--item a:active {
  color: var(--dark-text-grey);
  transition: 0.25s;
}
.nav__list--item a:after {
  display: block;
  content: "";
  width: 0.4375rem;
  height: 0.4375rem;
  margin: 0 auto;
  margin-top: 0.4375rem;
  border-radius: 50%;
  background-color: var(--accent-pink);
  opacity: 0;
}
.nav__list--item a:hover:after,
.nav__list--item a:active:after {
  opacity: 1;
  transition: all 0.35s;
}

.container__sub {
  padding-top: 6.25rem;
  display: flex;
  justify-content: space-around;
}
@media (max-width: 71.8125em) {
  .container__sub {
    flex-direction: column;
    align-items: center;
  }
}
@media (max-width: 39.9375em) {
  .container__sub {
    padding-top: 1.5625rem;
  }
}
.container__sub p.special-txt {
  margin-top: 0;
}
.container__sub p {
  font-weight: 100;
  max-width: 60ch;
  padding-bottom: 1.875rem;
}
@media (max-width: 39.9375em) {
  .container__sub p {
    text-align: center;
    width: 70%;
    margin: 0 auto;
  }
}
.container__sub h2 {
  font-weight: 400;
  font-size: 1.875rem;
  max-width: 60ch;
}
@media (max-width: 71.8125em) {
  .container__sub h2 {
    text-align: center;
  }
}
@media (max-width: 39.9375em) {
  .container__sub h2 {
    text-align: center;
    width: 70%;
    margin: 0 auto;
    padding-bottom: 1.5625rem;
  }
}
@media (max-width: 39.9375em) {
  .container__sub h2 {
    font-size: 25px;
  }
}
.container__sub--left {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding-right: 3.125rem;
  padding-bottom: 3.125rem;
}
@media (max-width: 71.8125em) {
  .container__sub--left {
    justify-content: center;
    align-items: center;
    padding-right: 0;
  }
}
.container__sub--left img {
  display: inline-block;
  width: 10.9375rem;
  border-radius: 50%;
  margin-bottom: 1.5625rem;
}
.container__sub--left h1 {
  font-size: 3.75rem;
  line-height: 1;
  padding-bottom: 1.5625rem;
}
@media (max-width: 39.9375em) {
  .container__sub--left h1 {
    font-size: 40px;
    text-align: center;
  }
}
.container__sub--left a.accent {
  padding-right: 1.25rem;
  font-size: 1.4375rem;
}
.container__sub--left a:not(:first-child) {
  color: var(--accent-pink);
}
.container__sub--left a:not(:first-child):hover i {
  transform: translateX(10px);
  transition: all 0.25s;
}
.container__sub--right {
  width: 60ch;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
@media (max-width: 71.8125em) {
  .container__sub--right {
    justify-content: center;
    text-align: center;
  }
}
@media (max-width: 39.9375em) {
  .container__sub--right {
    width: 95%;
  }
}
.container__sub--right p:first-child {
  letter-spacing: 0.125rem;
}
.container__sub--right p:not(:first-child) {
  padding-bottom: 1.875rem;
  font-weight: 200;
  line-height: 2;
}
@media (max-width: 39.9375em) {
  .container__sub--right p {
    width: 70%;
  }
}
.container__sub--right--pagenav {
  display: flex;
}
@media (max-width: 71.8125em) {
  .container__sub--right--pagenav {
    justify-content: center;
  }
}
.container__sub--right--pagenav i {
  padding-left: 10px;
}
.container__sub--right a {
  color: var(--accent-pink);
}
.container__sub--right a:hover i {
  transform: translateX(10px);
  transition: all 0.25s;
}

#socials {
  display: flex;
}
#socials a {
  font-size: 23px;
  color: var(--light-text-grey);
  padding-right: 20px;
}
#socials a:hover i {
  color: var(--accent-pink);
  transition: 0.25s all;
  transform: translateX(0);
}

.container__sub.about {
  padding-top: rem(50);
}
.container__sub.about a.accent {
  color: var(--accent-pink);
}

.skills__list {
  display: flex;
  flex-direction: column;
}
.skills__list h2 {
  padding-bottom: 50px;
}

.skill__item {
  display: flex;
  flex-direction: column;
}
.skill__item--bar {
  background-color: var(--darker-accent-pink);
  height: 5px;
  border-radius: 10px;
  margin-bottom: 30px;
}
.skill__item--percentage {
  display: block;
  height: 5px;
  border-radius: 10px;
  background-color: var(--accent-pink);
}
.skill__item .html {
  display: block;
  height: 5px;
  width: 90%;
}
.skill__item .css {
  display: block;
  height: 5px;
  width: 80%;
}
.skill__item .sass {
  display: block;
  height: 5px;
  width: 75%;
}
.skill__item .js {
  display: block;
  height: 5px;
  width: 75%;
}

.container__sub.projects a {
  color: var(--accent-pink);
  font-weight: 400;
}
.container__sub.projects a:hover i {
  transform: translateX(10px);
  transition: all 0.25s;
}

.projects__clients {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 15px;
  align-items: center;
}
@media (max-width: 39.9375em) {
  .projects__clients {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    padding-bottom: 4.6875rem;
  }
}
.projects__clients a img {
  width: 150px;
}

.container__sub.projects.components {
  display: block;
}
.container__sub.projects.components h2 {
  padding-bottom: 30px;
}

.component__description {
  display: none;
}

.components__grid {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(10, 5vw);
  gap: 30px;
}
.components__grid p {
  margin: 0;
  padding: 0;
}
.components__grid--item {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.components__grid--item figure, .components__grid--item img {
  display: block;
  margin: 0;
  padding: 0;
  border-radius: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.components__grid--item:hover {
  transform: scale(1.01);
}

.grid__item-1 {
  grid-column-start: 1;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 4;
}

.grid__item-2 {
  grid-column-start: 4;
  grid-column-end: 9;
  grid-row-start: 1;
  grid-row-end: 4;
}

.grid__item-3 {
  grid-column-start: 7;
  grid-column-end: 9;
  grid-row-start: 4;
  grid-row-end: 8;
}

.grid__item-5 {
  grid-column-start: 1;
  grid-column-end: 7;
  grid-row-start: 4;
  grid-row-end: 8;
}

.grid__item-6 {
  grid-column-start: 1;
  grid-column-end: 9;
  grid-row-start: 8;
  grid-row-end: 12;
}

.contact .landing {
  height: 50vh;
  margin-top: 25vh;
  padding-top: 20vh;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

form input {
  font-family: var(--main-font);
  letter-spacing: 1.5px;
  color: var(--light-text-grey);
  background-color: transparent;
  border: none;
  border-bottom: 1px solid var(--dark-text-grey);
  padding: 20px;
  margin-bottom: 40px;
}

input:nth-child(4) {
  margin-bottom: 50px;
}

input:last-child {
  border: none;
}

form input:focus {
  outline: none;
}

form textarea {
  background-color: var(--light-grey);
  color: var(--ligt-text-grey);
  font-family: var(--main-font);
  padding: 15px;
  letter-spacing: 1.5px;
}
form textarea:focus {
  outline: none;
}

form input[type=submit] {
  background-color: transparent;
  border: none;
  margin: 0 auto;
  display: flex;
  font-family: var(--main-font);
  color: var(--accent-pink);
  font-size: 16px;
  letter-spacing: 1.5px;
}
form input[type=submit]:hover {
  cursor: pointer;
  transform: translateX(7px);
}

form label {
  opacity: 0;
}

/* ERROR STYLING */
form input.error {
  background: url(./images/icon-error.svg) no-repeat 375px;
  padding-right: 20px;
  border: 2px solid var(--red);
  color: var(--red);
}

form p.error__text {
  text-align: right;
  padding: 10px 0 20px 0;
  font-style: italic;
  color: var(--red);
  font-size: 11px;
}

form input.normal {
  /* display: none; */
}

.hide {
  display: none;
}

.show {
  display: block;
}