html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, 
*::before, 
*::after {
  box-sizing: inherit;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--main-font);
  color: var(--light-text-grey);
}